import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';

import { FuseSharedModule } from '@fuse/shared.module';

const routes = [
    {
        path        : 'analytics',
        loadChildren: () => import('app/main/dashboard/analytics/analytics.module').then(m => m.AnalyticsDashboardModule)
    },
    {
        path        : 'project',
        loadChildren: () => import('app/main/dashboard/project/project.module').then(m => m.ProjectDashboardModule)
    },
    
];

@NgModule({
    imports     : [
        RouterModule.forChild(routes),
        FuseSharedModule
    ]
})
export class DashboardModule
{
}
